.main-allmembers-section {
    background-color: #150f22;
    padding-bottom: 120px;
}

.main-gradient {
    background-image: url("../../../../../assets/images/internalpages-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
}

.heading-div {
    padding: 93px 0px;

    h1 {
        color: #fff;
        font-family: Urbanist;
        font-size: 48px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }

    p {
        color: #fff;
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}


.contactus-section {
  position: relative; /* important for ::after positioning */
  border-radius: 24px;
  background: var(--new, linear-gradient(130deg, #492FB2 -1.74%, #ADADAD 108.02%));
  padding: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden; /* keeps the after image inside the box */
}

.contactus-section p {
  color: #FFF;
  font-family: Urbanist;
  font-size: 28px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  margin: 0px;
  width: 50%;
}

/* Add image with ::after */
.contactus-section::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 50%; /* adjust how much space image takes */
  background: url('../../../../../assets/images/ias-contact-rings.png') no-repeat center right;
  background-size: cover; /* make it responsive */
  border-top-right-radius: 24px;
  border-bottom-right-radius: 24px;
}
.future-vision {
  margin: 60px 0px 120px 0px;

  h4 {
    color: #fff;
    font-family: Urbanist;
    font-size: 16px;
    font-style: normal;
    font-weight: 200;
    line-height: 22.393px;
    /* 139.957% */
    text-transform: uppercase;
  }

  h1 {
    color: #fff;
    font-family: Urbanist;
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 60px;
    /* 150% */
    letter-spacing: -0.1px;
    margin: 0px;
  }

  .vision-cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 43px;
    margin-top: 50px;
  }

  .vision {
    position: relative;
    /* needed for ::after positioning */
    padding: 40px;
    height: max-content;
    border-radius: 37px;
    border: 1.5px solid var(--1, #6582ff);
    background: var(
      --1,
      linear-gradient(
        0deg,
        rgba(101, 130, 255, 0.18) 0%,
        rgba(192, 91, 218, 0.18) 100%
      )
    );
    overflow: hidden;

    /* hides ::after overflow */
    h1 {
color: #FFF;
font-family: Urbanist;
font-size: 26px;
font-style: normal;
font-weight: 600;
line-height: 43.691px; /* 168.041% */
letter-spacing: -0.073px;
display: flex;
align-items: center;
gap: 5px;
    }

    p {
color: #FFF;
font-family: Urbanist;
font-size: 18px;
font-style: normal;
font-weight: 400;
line-height: 43.691px; /* 242.727% */
letter-spacing: -0.073px;
    }
  }

  .vision:nth-of-type(2) {
    margin-top: 90px;
    /* adjust as needed */
  }

  .vision::after {
    content: "";
    position: absolute;
    width: 271px;
    height: 271px;
    background: url("../../../../../assets/images/vision-rings.png");
    top: 0px;
    right: 0px;
    background-position: right top;
    background-repeat: no-repeat;
    background-size: contain;
  }

  .white-circle {
    background-color: #fff;
    padding: 13px;
    border-radius: 999px;
    width: min-content;
  }
}

.tick-list {
  list-style: none; /* remove default bullets */
  padding-left: 0;
}

.tick-list li {
  position: relative;
  padding-left: 25px; /* space for the tick */
  margin-bottom: 10px;
  color: rgba(255, 255, 255, 0.78);
font-family: Urbanist;
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: 43.691px; /* 273.067% */
letter-spacing: -0.073px;
}

.tick-list li::before {
  content: 'âœ”'; /* tick symbol */
  position: absolute;
  left: 0;
  color: rgb(255, 255, 255); /* tick color */
  font-weight: bold;
}
  @media (max-width: 1000px){
